@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://use.typekit.net/rns0sjx.css");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 18px;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: "neuzeit-grotesk", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.7;
}

body h1 {
  font-size: 3rem;
  font-weight: 300;
}

body h2 {
  font-size: 2.5rem;
  font-weight: 400;
}

body h3 {
  font-size: 2rem;
  font-weight: 600;
}

body blockquote {
  margin-top: 1rem;
  font-size: 1.7rem;
  font-weight: 500;
  line-height: 1.3;
}

body blockquote:before {
  color: #ccc;
  content: '“';
  font-size: 4.5rem;
  line-height: 0.1rem;
  margin-right: 0.75rem;
  vertical-align: -1rem;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .row {
  display: -webkit-flex;
  display: flex;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  flex-direction: column;
}

.container {
  width: 100%;
}

.padding {
  padding: 16px;
}

.row {
  flex-direction: row;
  flex-wrap: wrap;
  margin: auto;
  justify-content: center;
}

.col-1 {
  flex-basis: 8%;
}

.col-2 {
  flex-basis: 16%;
}

.col-3 {
  flex-basis: 24%;
}

.col-4 {
  flex-basis: 32%;
}

.col-5 {
  flex-basis: 40%;
}

.col-6 {
  flex-basis: 48%;
}

.col-7 {
  flex-basis: 56%;
}

.col-8 {
  flex-basis: 64%;
}

.col-9 {
  flex-basis: 72%;
}

.col-10 {
  flex-basis: 80%;
}

.col-11 {
  flex-basis: 88%;
}

.col-12 {
  flex-basis: 96%;
}
